































import { Component, Vue } from 'vue-property-decorator';
import { Swiper, Slide } from 'vue-swiper-component';
@Component({
  components: {
    Swiper,
    Slide
  }
})
export default class Carousel extends Vue {

  ClickLeft() {
    this.$refs.swiper.prevSlide(); // 上一张图
  }
  ClickRight() {
    this.$refs.swiper.nextSlide(); // 下一张
  }
}
